*{
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
}
:root{
  --color-131921:#131921;
  --color-3b4149: #3b4149;
  --color-febd69: #febd69;
  --color-232f3e: #232f3e;
  --color-bf4800: #bf4800;
  --color-e0e0e3: #e0e0e3;
  --color-ededed: #ededed;
  --color-777777: #777777;
  --color-1c1c1b: #1c1c1b;
  --color-4cc6f6:#4cc6f6;
}
body{
  font-family: "Rubik",sans-serif;
}
a{
  text-decoration: none;
  display: inline-block;
}
.form-control:focus{
  box-shadow:none;
  border-color: inherit;
}
.section-heading{
  font-size: 26px;
  line-height: 32px;
  font-weight: 500;
  margin-bottom: 30px;
}
.gap-10{
  gap:10px;
}
.gap-15{
  gap:15px;
}
.gap-30 {
  gap: 30px;
}
.btnAll {
  background-color: var(--color-232f3e);
  color: white;
  font-size: 14px;
  padding: 8px 15px;
  border-radius: 25px;
  border-color: #fff;
  max-width: 150px;
  max-height: 40px;
  text-align: center;
  resize: none;
}
.btnAll:hover {
  background-color: transparent;
  color: #000;
  font-weight: bold;
  border: #000 solid;
  transition: .2s;
  resize: none;
}
.card-wrapper{
  background-color:white ;
  box-shadow:  0 0 20px #0000001a;
  padding: 15px;
}
/*====Header part=====*/
.header-top-strip{
  border-bottom: 1px solid var(--color-3b4149);
  background-color: var(--color-131921);background-color: var(--color-131921);
}
.header-upper{
  background-color: var(--color-131921);
}
.header-upper .input-group-text {
  background-color: var(--color-febd69);
}
.header-upper.header-upper-links img {
  width: 35px;
  height: 35px;
}
.header-bottom {
  background-color: var(--color-232f3e);
}
.menu-links{
  margin: right;
}
.header-bottom .menu-links a,
.header-bottom .dropdown button {
  color: white;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  letter-spacing: 0.3;
  text-transform: uppercase;
}
.header-bottom .dropdown button:focus {
  box-shadow: none;
}
.header-bottom .dropdown-menu {
  background-color: var(--color-131921);
  width: 100%;
 /* // transform: translate3d(0px, 57px, 0px); */
}
.header-bottom .dropdown-item {
  padding: 20px;
  margin-bottom: 3px;
  border-bottom: 1px solid var(--color-3b4149);
}
.header-bottom .dropdown-item:hover {
  background-color: transparent;
  color: var(--color-febd69);
}
/*====Header End=====*/

/* Footer Part */

footer {
  background-color: var(--color-232f3e);
}

footer:not(:first-child) {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
footer .input-group-text {
  background-color: var(--color-232f3e);
  color: white;
  padding: 8px 17px;
}
/* Footer Part Ends Here */


/* Home Page Start */
.main-banner-content {
  top: 15%;
  left: 5%;
}
.main-banner-content h4 {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #000;
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  background-color: var(--color-4cc6f6);
  width: 120px;
}
.main-banner-content h5 {
  font-size: 42px;
  line-height: 54px;
  letter-spacing: -2px;
  font-weight: 500;
  text-transform: none;
  background-color: var(--color-4cc6f6);
  margin: 0 auto;
  padding: 1px 2px;
}

.main-banner-content p {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.4;
  color: #000;
}
.ba {
  width:100%;
 height: 81.3vh;
}
.small-banner-content {
  top: 25%;
  left: 10%;
}
.small-banner-content h4 {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-bf4800);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}
.small-banner-content h5 {
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -2px;
  font-weight: 500;
  text-transform: none;
  background-color: var(--color-4cc6f6);
  text-align: center;
}
.ba-small {
  width:100%;
 height: 39.5vh;
}

.small-banner-content p {
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}
.small-banner {
  width: 49%;
}
.home-wrapper-2{
  background-color: var(--color-e0e0e3);
}
.mimg{
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: var(--color-4cc6f6) 2px solid;
}
/* Home Page End */


/* Blog part Start */
.blog-card{
  background-color: white;
  border-radius: 10px;
  padding: 0;
}
.blog-card img{
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
  height: 250px;
}
.blog-content{
  padding: 15px;
}
.blog-contentp.date{
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0;
  font-weight: 400;
  color: var(--color-777777);
  padding: 0;
}
.blog-content h5{
  font-size: 18px;
  color: var(--color-131921);
}
.blog-content p.desc{
  font-size: 13px;
  line-height: 22px;
  color: var(--color-777777);
}

/* Blog part End */

/* Common Medicine part Start */
.medicine-card{
  padding: 0;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  height: auto;
}
.medicine-card img{
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
  height: 235px;
}

.medicine-card .medicine-details {
padding: 15px;
height: 150px;
}
.medicine-card .medicine-details .medicine-title {
  height: 30px;
  margin-bottom: 0px;
}
.medicine-card .medicine-details h6{
  font-size: 13px;
  color: var(--color-bf4800);
}
.medicine-card .medicine-details h5{
  font-size: 15px;
  color: var(--color-1c1c1b);
  margin-bottom: 5px;
}
.medicine-card .medicine-details p.price{
  margin-top: 5px;
  font-size: 16px;
  color: var(--color-1c1c1b);
  /* float: left; */
}
.cart{
  display: flex;
  bottom: 30px;
  align-items: center;

}

/* Common Medicine part End */
