/* Food Card Start */
.food-card{
  padding: 0;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
}
.food-card:hover .action-bar{
  right: 0px;
}

.food-card img{
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
  height: 230px;
}

.food-card .food-details {
padding: 15px;
height: 150px;
}
.food-card .food-details .food-title {
  height: 30px;
  }
.food-card .food-details h6{
  font-size: 13px;
  color: var(--color-bf4800);
}
.food-card .food-details h5{
  font-size: 15px;
  color: var(--color-1c1c1b);
}
.food-card .food-details p.price{
  font-size: 16px;
  color: var(--color-1c1c1b);
}
.description{
  font-size: 13px;
