}

/* Medicine page End here */

/* Contact page Start here */
.contact-inner-wrapper{
  padding: 30px 20px;
  border-radius: 15px;
  gap: 15px;
  background-color: white;
}
.contact-inner-wrapper>div{
  width: 48%;
}
.contact-title{
  font-size: 26px;
  font-weight: 500;
  line-height: 35px;
  text-align: center;
}
.contact-inner-wrapper form .form-control{
  border-radius: 10px;
  background-color: #f5f5f7;
  border-color: transparent;
  padding: 10px;
}
.res{
  text-align: center;
  color: #000;
}
.res>a{
  color: #000;
}

/* Contact page End here */

/* Login Page here */
.auth-card{
  padding: 20px;
  background-color: white;
  width: 500px;
  margin: 30px auto;
}
.auth-card h3{
  font-size: 19px;
  font-weight: 500;
  color: var(--color-777777);
}
.auth-card .form-control{
  height: 50px;
  background-color: #f5f5f7;
  color: var(--color-777777);
  font-size: 14px;
  border-radius: 10px;
  border-color: transparent;
}
.auth-card a{
  font-size: 14px;
  font-weight: 400;
  color: var(--color-1c1c1b);
}
.auth-card .signup{
  background-color: var(--color-febd69);
  color: var(--color-131921);
}
.auth-card .signup:hover{
  background-color: var(--color-131921);
  color: white;
}
/*Product list start*/

.prod-wrapper{
  background-color:white ;
  display: flex;
  align-items: center;
  justify-content: center;
}
.product-div{
  padding: 2rem 0;
  margin: 1rem 0;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  background-color: #fff;
  border-radius: 10px;
}
.product-div-left{
  padding:20px;
}
.product-div-right{
  padding:20px;
}
.img-container img{
  width: 600px;
  height: 500px;
  margin: 0 auto;
}
.hover-container {
  display:flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top:32px;
}
.hover-container div{
  border: 2px solid rgba(252,160,175,0.7);
  padding: 1rem;
  border-radius: 3px;
  margin: 0 4px 8px 4px;
  display:flex;
  align-items: center;
  justify-content: center;
}
.active{
  border-color: rgb(255, 145, 163);
}
.hover-container div:hover{
  border-color: rgb(255, 145, 163);
}
.hover-container div img{
  width: 50px;
  cursor: pointer;
}
.product-div-right span{
  display: block;
} 
.product-name{
  font-size: 30px;
  margin-bottom: 25px;
  font-weight: 700;
  letter-spacing: 1px;
  opacity: 0.9;
}
.product-price{
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 1px;
  opacity: 0.9;
}
.product-rating {
  display:flex;
  align-items: center;
  margin-top: 12px;
}
.product-rating span{
  margin-right: 6px;
}
.product-description{
  font-size: 20px;
  font-weight: 300;
  line-height: 1.6;
  opacity: 0.6;
  align-content:start;
  margin-top: 22px;
}
.prod-image{
  border-radius: 10px;
  height: 300px;
  width: 400px;
}

/* Shop page start here */
.timg{
  width: 100%;
  height: 200px;
  object-fit: cover;
  margin-bottom: 20px;
}
.rname{
  font-size: 25px;
  margin-bottom: 20px;
  font-weight: bold;
  color:rgb(238, 93, 74);
  margin-left: 20px;
}
.P_image{
  width:100%;
  height: 150px;
}
.top{
  display:grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 14px;
  row-gap: 20px;
}
.t1{
  display:flex;
  flex-direction: column;
  max-height: 280px;
  border:none;
  box-shadow: 0px 0px 4px rgb(197, 186, 186);
  min-width: 210px;
  padding:10px 10px;
  border-radius: 5px;
}
.h1{
  font-size: 20px;
  font-family: Rubic;
  font-weight: 600;
  margin-bottom: 10px;
  margin-top: 10px;
}
.d1{
  font-size: 17px;
  color:rgb(102, 98, 98);
  margin-bottom: 10px;
  height: 40px;
  overflow:hidden;

}
.tk{
  font-size: 20px;
  font-weight: 400;
  display: inline-block;
  margin-right: 20px;
}
.bt1{
  padding:8px 7px;
  font-size: 15px;
  border-radius: 5px;
  cursor:pointer;
  background-color: rgb(245, 231, 191);
  font-weight: bold;
  border:none;
  transition: 0.5s;
}
.bt1:hover{
  background-color: rgb(240, 217, 149);
} 
.outs{
  background-color: white;
  padding:20px 20px;
  margin-bottom: 20px;
}
.tag1{
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 4px;
}
.tag2{
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
}
.tagd1{
  font-size: 20px;
  color:rgb(165, 160, 160);
  margin-bottom: 20px;
}
@media (max-width: 760px){
  .top{
       grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1000px){
  .top{
       grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}


